.menu {
  display: block;
  position: fixed;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  top: 0;
  text-align: right;
  z-index: 0;
  transition: background-color 1s, z-index 0.5s;
}
.menu.menuShow {
  right: 0;
  z-index: 2;
  background-color: var(--color-background-popup);
}

.menuPopUpContent {
  display: none;
  background-color: var(--color-primary);
  margin: 0 0 auto auto;
  margin-right: -466px;
  height: 100vh;
  width: 466px;
  z-index: 2;
  transition: margin-right 0.5s;

  &.menuPopUpContentShow {
    margin-right: 0;
  }

  @media (max-width: 550px) {
      width: 252px;
  }

  .menuIconClose {
    display: block;
    align-self: flex-start;
    cursor: pointer;
  }

  .menuThemeWrapp {
    display: flex;
    justify-content: center;
    justify-self: start;
    align-items: center;
    padding-left: 94px;
    cursor: pointer;

    @media (max-width: 550px) {
        padding-left: 47px;
    }

    .menuThemeMode {
        font-family: var(--font-family-paragraph);
        color: var(--color-primary-grey);
        text-transform: uppercase;
        text-decoration: underline;
        margin-left: 12px;
        font-size: 12px;
        line-height: 15px;
        margin-right: 12px;
    }
  }
}

.menu .menuPopUpContent {
  padding-top: 34px;
  padding-right: 28px;
  display: grid; 
  justify-items: flex-end;
  align-items: flex-end;
  grid-template-rows: 52px 204px 132px 100px;

  @media (max-width: 320px) {
      padding-right: 24px;
      padding-top: 24px;
  }

  @media (max-width:550px) {
      grid-template-rows: 52px 204px 72px 64px;
  }
}
